<template>
  <!-- <v-container row justify-end> -->
  <v-dialog v-model="dialog" persistent xs12 sm6 offset-sm3 max-width="400px">
    <template v-slot:activator="{ on }">
      <v-btn class="dugme primary" dark v-on="on">
        Registracija
        <v-icon>mdi-login</v-icon>
      </v-btn>
    </template>
    <!-- <v-layout row> -->
    <!-- <v-flex xs12 sm6 offset-sm3> -->
    <v-card>
      <div class="text-center pt-4 title">
        Registracija
        <p>- jos nije podeseno-</p>
      </div>
      <v-card-text xs12 sm6 offset-sm3>
        <v-container>
          <form @submit.prevent="onSignup">
            <v-layout row>
              <v-flex xs-12>
                <v-text-field
                  name="email"
                  label="Mail"
                  id="email"
                  v-model="email"
                  type="email"
                  required
                ></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row>
              <v-flex xs-12>
                <v-text-field
                  name="password"
                  label="Password"
                  id="password"
                  v-model="password"
                  type="password"
                  required
                ></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row>
              <v-flex xs-12>
                <v-text-field
                  class="mb-4"
                  name="confirmPassword"
                  label="Confirm password"
                  id="confirmPassword"
                  v-model="confirmPassword"
                  type="password"
                  :rules="[comparePasswords]"
                ></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row>
              <v-flex d-flex xs12>
                <v-btn type="submit">Registracija</v-btn>
                <v-spacer></v-spacer>
                <v-btn text @click="dialog = false">Nazad</v-btn>
              </v-flex>
            </v-layout>
          </form>
        </v-container>
      </v-card-text>
    </v-card>
    <!-- </v-flex> -->
    <!-- </v-layout> -->
  </v-dialog>
  <!-- </v-container> -->
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
      email: "",
      password: "",
      confirmPassword: ""
    };
  },
  computed: {
    comparePasswords() {
      return this.password !== this.confirmPassword
        ? "Sifre se ne poklapaju"
        : "";
    }
  },
  methods: {
    // onSignup() {
    //   this.$store.dispatch("SignUserUp", {
    //     email: this.email,
    //     password: this.password
    //   });
    // }
  }
};
</script>

